import React from "react"
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Box,
  makeStyles,
  Typography,
  Hidden,
  Grid,
} from "@material-ui/core"
import ExpandMoreIcon from "@material-ui/icons/ExpandMore"
import Post from "./post"
import {
  IndexPageQuery_allMarkdownRemark_edges,
  IndexPageQuery_site_siteMetadata_categories,
} from "../../pages/__generated__/IndexPageQuery"

const useStyles = makeStyles(theme => ({
  heading: {
    fontSize: theme.typography.pxToRem(15),
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
}))
interface ChildProps {
  //  
  category: IndexPageQuery_site_siteMetadata_categories
  // 
  posts: IndexPageQuery_allMarkdownRemark_edges[]
}
//    
const PostsPerCategory: React.FC<ChildProps> = ({ category, posts }) => {
  const classes = useStyles()

  // Category 
  const displayCategoryName = (name: string) => {
    let displatName = name
    switch (name.toLowerCase()) {
      case "frontend":
        displatName = " " + displatName
        break
      case "backend":
        displatName = " " + displatName
        break
      case "devops":
        displatName = " " + displatName
        break
      case "tools":
        displatName = " " + displatName
        break
      case "others":
        displatName = " " + displatName
        break
      case "all posts":
        displatName = " All Posts"
        break
      default:
    }
    return displatName
  }

  return (
    <Accordion>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls={category.name + "-content"}
        id={category.name + "-header"}
      >
        <Box flexGrow={1}>
          <Typography className={classes.heading}>
            {displayCategoryName(category.name!)}
          </Typography>
        </Box>
        {/* Category child  xsDown -> smDown  */}
        <Hidden xsDown>
          <Typography className={classes.secondaryHeading}>
            {category.children &&
              category.children.map(child => {
                const { name } = child!
                const upperName = name!.charAt(0).toUpperCase() + name!.slice(1)
                return upperName + " / "
              })}
            {" " + posts.length} posts
          </Typography>
        </Hidden>
      </AccordionSummary>
      <AccordionDetails>
        <Grid container spacing={1}>
          {posts.map(({ node }, i) => {
            return <Post key={i} node={node} />
          })}
        </Grid>
      </AccordionDetails>
    </Accordion>
  )
}

export default PostsPerCategory
